<template>
  <div class="message">
    <mt-header fixed title="我的消息">
      <div class="h_left" slot="left">
        <i @click="back" class="iconfont icon-fanhui"></i>
      </div>
    </mt-header>
    <div class="container">
      <mt-navbar v-model="selected">
        <mt-tab-item id="1">通知</mt-tab-item>
        <mt-tab-item id="2">邀请</mt-tab-item>
        <mt-tab-item id="3">点赞</mt-tab-item>
        <mt-tab-item id="4">评论</mt-tab-item>
        <mt-tab-item id="5">关注</mt-tab-item>
      </mt-navbar>

      <!-- tab-container -->
      <mt-tab-container v-model="selected">
        <mt-tab-container-item id="1">
          <div class="info">
            <div class="total">
              共{{infonum}}条
            </div>
            <ul>
              <li @click="toEssay(item.aid)" v-for="(item,index ) in info" :key="index">
                <div class="msg">{{item.msg}}</div>
                <!--<div class="date">2019/03/05 10:34</div>-->
              </li>
            </ul>
          </div>
        </mt-tab-container-item>
        <mt-tab-container-item id="2">
          <div class="info">
            <div class="total">
              共{{invitenum}}条
            </div>
            <ul>
              <li @click="toEssay(item.aid)" v-for="(item,index ) in inviteData" :key="index">
                <div class="msg">{{item.msg}}</div>
                <!--<div class="date">2019/03/05 10:34</div>-->
              </li>
            </ul>
          </div>
        </mt-tab-container-item>
        <mt-tab-container-item id="3">
          <div class="info">
            <div class="total">
              共{{likenum}}条
            </div>
            <ul>
              <li @click="toEssay(item.aid)" v-for="(item,index ) in likedata" :key="index">
                <div class="msg">{{item.msg}}</div>
                <!--<div class="date">2019/03/05 10:34</div>-->
              </li>
            </ul>
          </div>
        </mt-tab-container-item>
        <mt-tab-container-item id="4">
          <div class="info">
            <div class="total">
              共{{commentnum}}条
            </div>
            <ul>
              <li @click="toEssay(item.aid)" v-for="(item,index ) in commentData" :key="index">
                <div class="msg">{{item.msg}}</div>
                <!--<div class="date">2019/03/05 10:34</div>-->
              </li>
            </ul>
          </div>
        </mt-tab-container-item>
        <mt-tab-container-item id="5">
          <div class="info">
            <div class="total">
              共{{attentionnum}}条
            </div>
            <ul>
              <li @click="toEssay(item.aid)" v-for="(item,index ) in attentionData" :key="index">
                <div class="msg">{{item.msg}}</div>
                <!--<div class="date">2019/03/05 10:34</div>-->
              </li>
            </ul>
          </div>
        </mt-tab-container-item>
      </mt-tab-container>
    </div>
  </div>
</template>

<script>
  import {message} from '@/service/getData'
  import {mapState} from 'vuex'

  export default {
    name: "message",
    data() {
      return {
        selected: '1',
        info: [],
        infonum: 0,
        likedata: [],
        likenum: 0,
        commentData: [],
        commentnum: 0,
        attentionData: [],
        attentionnum: 0,
        inviteData: [],
        invitenum: 0
      }
    },
    computed: {
      ...mapState([
        'userInfo',
        'login'
      ]),
    },
    mounted() {
      this.getMessage()
    },
    methods: {
      //返回
      back() {
        this.$router.go(-1);
      },
      //跳往问题详情页面
      toEssay(aid) {
        this.$router.push({path: 'essay', query: {aid}})
      },
      //获取通知
      async getMessage() {
        let res = await message(this.userInfo.uid);
        this.info = res;
        this.infonum = res.length;
        this.info.map((value, index, array) => {
          if (value.type === 1) {
            this.likedata.push(value)
          } else if (value.type === 3) {
            this.commentData.push(value)
          } else if (value.type === 5) {
            this.attentionData.push(value)
          } else if (value.type === 6) {
            this.inviteData.push(value)
          }
        });
        setTimeout(()=>{
          this.likenum = this.likedata.length;
          this.commentnum = this.commentData.length;
          this.attentionnum = this.attentionData.length;
          this.invitenum = this.inviteData.length;
        },200)
      },
    }
  }
</script>

<style scoped lang="scss">
  .message {
    .container {
      padding-top: 40px;
      flex: 1;

      /deep/ .mint-navbar {
        border-bottom: 1px solid rgba(216, 216, 216, 1);
        padding-bottom: .05rem;
      }

      /deep/ .mint-tab-container {
        padding: 0 .36rem;
        box-sizing: border-box;
      }

      .total {
        padding: .28rem 0;
        font-size: 0.28rem;
        font-weight: 400;
        color: rgba(102, 102, 102, 1);
        line-height: 0.4rem;
        letter-spacing: 1px;
      }

      .info {
        width: 100%;

        ul {
          width: 100%;
          display: flex;
          flex-direction: column;

          li {
            width: 100%;
            padding: .28rem 0;
            border-bottom: 1px solid rgba(238, 238, 238, 1);

            .msg {
              width: 100%;
              font-size: 0.28rem;
              font-weight: 600;
              color: rgba(51, 51, 51, 1);
              line-height: 0.4rem;
            }

            .date {
              font-size: 0.24rem;
              font-weight: 400;
              color: rgba(153, 153, 153, 1);
              line-height: 0.34rem;
            }
          }
        }
      }
    }
  }
</style>



