<template>
  <div class="quiz">
    <mt-header fixed title="提问">
      <div class="h_left" slot="left">
        <i @click="back" class="iconfont icon-fanhui"></i>
      </div>
    </mt-header>
    <div class="container">
      <div v-show="showBox==1" class="box1">
        <div class="tips">添加标题(最多40字)</div>
        <div class="input_box">
          <input v-model="searchValue" placeholder="请输入标题" type="search" autofocus>
        </div>
        <div class="recommend">
          <ul>
            <li v-for="(item,index) in searchList" @click="select(item.title)" :key="index">{{item.title}}</li>
          </ul>
        </div>
        <div class="btn">
          <mt-button @click="showBox=2" :disabled="ablenext" type="primary">下一步</mt-button>
        </div>
      </div>
      <div v-show="showBox==2" class="box2">
        <div class="title">{{searchValue}}</div>
        <div class="text">
          <textarea placeholder="添加问题背景描述(选填)" v-model="summary" name="sunmary" id="sunmary" cols="30"
                    rows="10"></textarea>
        </div>
        <div class="img_title">
          添加图片
        </div>
        <div class="img_box">
          <ul class="imgs">
            <li class="img" v-for="(item,index) in imgs" :key="index">
              <img :src="item.img" alt="">
            </li>
          </ul>

          <div class="upload" v-if="imgs.length<3">
            <i class="iconfont icon--jia"></i>
            <input type="file" name="image" @change="toupload($event)" accept="image/jpeg,image/x-png,image/gif">
          </div>

        </div>


        <div class="btn">
          <mt-button @click="showBox=1" style="margin-right: .5rem;">上一步</mt-button>
          <mt-button type="primary" @click="submit()">提交</mt-button>
        </div>
      </div>

    </div>
  </div>
</template>

<script>  import {mapState} from 'vuex'
import {getStore, setStore} from '@/config/mUtils'
import {imgUpload, searchRestaurant, toQuiz} from '@/service/getData'
import {fileBaseUrl} from '@/config/env'

export default {
  name: "quiz",
  data() {
    return {
      searchValue: '',
      searchList: [],
      showBox: '1',
      ablenext: true,
      summary: '',
      imgs: [],
    }

  },
  watch: {
    //字节判断
    searchValue(val, old) {
      if (val) {
        this.getList();
      }
      if (val == '') {
        this.searchList = [];
        this.searchList = this.searchList.slice();
      }
      var len = 0;
      for (var i = 0; i < val.length; i++) {
        var a = val.charAt(i);
        if (a.match(/[^\x00-\xff]/ig) != null) {
          len += 2;
        } else {
          len += 1;
        }
      }
      if (8 <= len && len <= 80) {
        this.ablenext = false;
      } else {
        this.ablenext = true;

      }
    },
  },
  computed: {
    ...mapState([
      'userInfo',
      'login'
    ]),
  },
  mounted() {

  },
  methods: {
    //返回
    back() {
      this.$router.go(-1);
    },
    //选择搜索结果
    async select(value) {
      this.searchValue = value;
    },
    //获取问题推荐列表
    async getList() {
      let res = await searchRestaurant(this.searchValue);
      this.searchList = [];
      this.searchList = this.searchList.slice();
      if (res.length > 0) {
        this.searchList = res;
      } else {
      }
    },

    //图片上传
    toupload(el) {
      var file = el.target.files[0];//name: "dangqi1.png" || type: "image/png"
      var type = file.type.split('/')[0];
      if (type === 'image') {
        //将图片img转化为base64
        var reader = new FileReader();
        reader.readAsDataURL(file);
        var that = this;
        reader.onloadend = function () {
          var dataURL = reader.result;
          var blob = that.dataURItoBlob(dataURL);
          that.upload(blob); //执行上传接口
        };
      } else {
        alert('上传了非图片');
      }
    },
    dataURItoBlob(dataURI) {
      // base64 解码
      let byteString = window.atob(dataURI.split(',')[1]);
      let mimeString = dataURI.split(',')[0].split(':')[1].split(';')[0];
      let T = mimeString.split('/')[1];
      let ab = new ArrayBuffer(byteString.length);
      let ia = new Uint8Array(ab);
      for (let i = 0; i < byteString.length; i++) {
        ia[i] = byteString.charCodeAt(i);
      }
      return new Blob([ab], {type: mimeString});
    },
    upload(imgUrl) {
      var formdata = new FormData();
      formdata.append('image', imgUrl);//下面是要传递的参数
      this.$axios.post(fileBaseUrl+"/index/Update/upload",
        formdata,
        {headers: {'Content-Type': 'multipart/form-data'}}
      ).then((res) => {
        this.imgs.push({img: res.data.url})
      })
    },
    //问题提问
    async submit() {
      let title = this.searchValue;
      let uid = this.userInfo.uid;
      let username = this.userInfo.username;
      for (var i = 0; i < this.imgs.length; i++) {
        if (this.imgs.length === 1) {
          var pic1 = this.imgs[i].img;
        } else if (this.imgs.length === 2) {
          var pic1 = this.imgs[i].img;
          var pic2 = this.imgs[i].img;
        } else {
          var pic1 = this.imgs[i].img;
          var pic2 = this.imgs[i].img;
          var pic3 = this.imgs[i].img;
        }
      }
      let summary = this.summary;
      let res = await toQuiz(title, uid, username, pic1, pic2, pic3, summary);
      if (res.result == 200) {
        this.$toast(res.msg);
        this.$router.push('/home');
      } else {
        this.$toast(res.msg)
      }
    }
  }
}
</script>

<style scoped lang="scss">
  .container {
    padding-top: 40px;
    flex: 1;

    .box1 {
      .tips {
        padding: .46rem .36rem;
        font-size: 0.28rem;
        font-weight: 600;
        color: rgba(51, 51, 51, 1);
        line-height: 0.4rem;
      }

      .input_box {
        width: 100%;
        padding: 0 .36rem .3rem;
        border-bottom: 1px solid rgba(238, 238, 238, 1);

        input {
          width: 100%;
          margin: 0 auto;
          height: 0.72rem;
          line-height: 0.72rem;
          background: rgba(255, 255, 255, 1);
          border-radius: 0.08rem;
          border: 0.02rem solid rgba(221, 221, 221, 1);

        }
      }

      .recommend {
        height: 3.6rem;
        overflow-y: scroll;

        ul {
          padding: 0.2rem .38rem;

          li {
            height: .6rem;
            line-height: .6rem;
            font-size: 0.28rem;
            font-weight: 400;
            color: rgba(51, 51, 51, 1);
          }
        }
      }

      .btn {
        margin-top: 1rem;
        width: 100%;
        display: flex;
        padding: 0 .36rem;
        justify-content: flex-end;
      }
    }

    .box2 {
      padding: 0 .36rem;

      .title {
        padding: .46rem .36rem;
        font-size: 0.28rem;
        font-weight: 600;
        color: rgba(51, 51, 51, 1);
        line-height: 0.4rem;
      }

      .text {
        width: 100%;
        height: 2.96rem;
        background: rgba(255, 255, 255, 1);
        border-radius: 0.08rem;
        border: 0.02rem solid rgba(221, 221, 221, 1);
        padding: .3rem;

        textarea {
          width: 100%;
          height: 100%;
          overflow-y: scroll;
        }
      }

      .img_title {
        padding: .3rem 0;
        height: 0.4rem;
        font-size: 0.28rem;
        font-weight: 600;
        color: rgba(51, 51, 51, 1);
        line-height: 0.4rem;
      }


      .img_box {
        display: flex;

        .imgs {
          display: flex;
          justify-content: flex-start;

          .img {
            margin-right: .2rem;
            width: 1.86rem;
            height: 1.86rem;

            img {
              width: 100%;
              height: 100%;
            }
          }
        }

        margin-top: .3rem;

        .upload {
          position: relative;
          width: 1.86rem;
          height: 1.86rem;
          background: rgba(238, 238, 238, 1);
          border: 0.02rem solid rgba(221, 221, 221, 1);
          /*display: flex;*/
          /*justify-content: center;*/
          /*align-items:  center;*/
          i {
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
            line-height: 1.8rem;
            text-align: center;
            color: #fff;
            font-size: 1rem;
            z-index: 1;
          }

          input {
            z-index: 1000;
            width: 100%;
            height: 100%;
            opacity: 0;
            /*visibility: hidden;*/
            position: absolute;
            left: 0;
            top: 0;
          }
        }

      }

      .btn {
        margin-top: .5rem;
        width: 100%;
        display: flex;
        justify-content: flex-end;
      }

    }
  }
</style>



