import {
  SHOW_SILDER_BAR,
  RECORD_USERINFO,
  OUT_LOGIN,
  GET_USERINFO
} from './mutation-types.js'

import {setStore, getStore,removeStore} from '../config/mUtils'
// import {localapi, proapi} from 'src/config/env'

export default {

  //修改下单参数
  // [CHANGE_ORDER_PARAM](state, newParam) {
  // 	state.orderParam = Object.assign({}, state.orderParam, newParam);
  // },

  //左边菜单栏显示,隐藏
  [SHOW_SILDER_BAR](state, newParam) {
    state.showSidebar = newParam.flag;
  },

  // 记录用户信息
  [RECORD_USERINFO](state, info) {
    state.userInfo = info;
    state.login = true;
    setStore('userInfo', info);
    // setStore('uid', info.uid);
  },
  //退出登录
  [OUT_LOGIN](state) {
    console.log("退出登录");
    state.userInfo = {};
    state.login = false;
    removeStore('userInfo')
  },
  //获取用户信息存入vuex
  [GET_USERINFO](state, info) {
    if (state.userInfo && (state.userInfo.username !== info.username)) {
      return;
    };
    if (!state.login) {
      return
    }
    if (!info.message) {
      state.userInfo = {...info};
    } else {
      state.userInfo = null;
    }
  },
}



