/**
 * 配置编译环境和线上环境之间的切换
 *
 * baseUrl: 域名地址
 * routerMode: 路由模式
 * imgBaseUrl: 图片所在域名地址
 *
 */

let baseUrl = 'api/';
let routerMode = 'hash';
let imgBaseUrl = '';
let fileBaseUrl = '';


if (process.env.NODE_ENV == 'development') {
  imgBaseUrl = 'http://www.yeschains.com/';
  fileBaseUrl = '/api';

} else if (process.env.NODE_ENV == 'production') {
  baseUrl = 'http://www.yeschains.com/';
  imgBaseUrl = 'http://www.yeschains.net/';
  fileBaseUrl = 'http://www.yeschains.net/';
}

export {
  baseUrl,
  routerMode,
  fileBaseUrl,
  imgBaseUrl,
}



