<template>
  <div class="register">
    <div class="top">
      <div class="logo">小盈问答</div>
      <div class="form">
        <div class="title">注册</div>
        <mt-field disableClear :attr="{ maxlength: 11 }" placeholder="请输入手机号" type="tel" v-model="mobile"></mt-field>
        <mt-field disableClear placeholder="请输入验证码" type="number" v-model="code">
          <div v-if="sendAuthCode" class="codetip" @click="getCode">
            获取验证码
          </div>
          <div v-else class="codetip" @click="getCode">
            {{auth_time}}秒重新发送
          </div>

        </mt-field>
        <mt-field disableClear placeholder="请输入密码" type="password" v-model="password"></mt-field>
        <mt-field disableClear placeholder="请确认密码" type="password" v-model="password2"></mt-field>
        <div class="btn">
          <mt-button class="tologin" type="primary" @click="toRegister">注册</mt-button>
        </div>
        <router-link to="login" class="otherlogin">已有账号</router-link>
      </div>
      <div class="tips">第三方登录</div>
    </div>
    <div class="bottom">
      <div class="other">
        <div class="qq">
          <i style="color: #25b5ff;" class="iconfont icon-qq"></i>
        </div>
        <div class="weixin">
          <i style="color: #00cd20;" class="iconfont icon-weixin1"></i>
        </div>
        <div class="weibo">
          <i style="color:#ff3b3b;" class="iconfont icon-weibo"></i>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {imgBaseUrl} from '@/config/env'
  import {mapMutations} from 'vuex'
  import {mobileCode, register} from '@/service/getData'

  export default {
    name: "register",
    data() {
      return {
        mobile: '18279198701',
        code: '123456',
        password: '123456',
        password2: '123456',
        sendAuthCode: true,
        auth_time: "",
        userInfo:null,
      }
    },
    mounted() {

    },
    methods: {
      ...mapMutations([
        'RECORD_USERINFO',
      ]),
      //获取验证吗
      async getCode() {
        var reg = /^(13[0-9]|15[012356789]|17[0-9]|18[0-9]|14[57])[0-9]{8}$/;
        if (!this.mobile) {
          this.$toast("请输入手机号码");
        } else if (!reg.test(this.mobile)) {
          this.$toast("请填写有效的手机号！");
        } else if (this.mobile) {
          let res = await mobileCode(this.mobile,1);
          if (res.result == 200) {
            this.$toast(res.msg);
            this.sendAuthCode = false;
            this.auth_time = 60;
            var auth_timetimer = setInterval(() => {
              this.auth_time--;
              if (this.auth_time <= 0) {
                this.sendAuthCode = true;
                clearInterval(auth_timetimer);
              }
            }, 1000);
          } else {
            this.$toast(res.msg);
          }
        } else {
          this.$toast("请输入手机号码");
        }
      },
      //注册
      async toRegister(){
        var reg = /^(13[0-9]|15[012356789]|17[0-9]|18[0-9]|14[57])[0-9]{8}$/;
        if (!this.mobile) {
          this.$toast("请输入手机号码");
        } else if (!reg.test(this.mobile)) {
          this.$toast("请填写有效的手机号！");
        } else if (!this.password || !this.password2) {
          this.$toast("请输入密码");
        } else if (this.password !== this.password2) {
          this.$toast("密码不一致");
        } else if (!this.code) {
          this.$toast("请输入验证码");
        } else {
         let res= await register(this.mobile,this.password,this.code,);
          if (res.stats) {
            this.$toast("注册成功");
            this.userInfo = res.user;
            if (!this.userInfo.uid) {
              this.$toast('未知错误,请重试');
            } else {
              this.userInfo.head= imgBaseUrl + "/uc_server/avatar.php?uid=" + this.userInfo.uid + "&amp;size=big";
              this.userInfo = Object.assign({},this.userInfo);
              this.RECORD_USERINFO(this.userInfo);
              this.$router.push('/home');
            }
          } else {
            this.$toast(res.msg);
          }
        }
      }
    }
  }
</script>

<style scoped lang="scss">
  .register {
    display: flex;
    flex-direction: column;

    .top {
      width: 100vw;
      height: 11.3rem;
      background: linear-gradient(180deg, rgba(49, 148, 255, 1) 0%, rgba(0, 86, 215, 1) 100%);
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;

      .logo {
        margin: 1rem auto 1rem;
        width: 2.26rem;
        height: .46rem;
        color: #fff;
        text-align: center;
        font-size: 20px;

      }

      .form {
        width: 5.92rem;
        height: 8rem;
        background: rgba(255, 255, 255, 1);
        box-shadow: 0px 11px 11px 0px rgba(0, 0, 0, 0.1);
        border-radius: 10px;
        display: flex;
        flex-direction: column;
        align-items: center;

        .title {
          height: 0.56rem;
          font-size: .4rem;
          font-weight: 400;
          color: rgba(51, 51, 51, 1);
          line-height: 0.56rem;
          margin: .30rem auto .40rem;
        }

        .btn {
          margin: 0 auto;

          .tologin {
            margin-top: .4rem;
            width: 5.2rem;
            height: 0.84rem;
            background: linear-gradient(90deg, rgba(49, 148, 255, 1) 0%, rgba(0, 86, 215, 1) 100%);
            border-radius: 0.42rem;
          }
        }

        .register {
          margin-top: .2rem;
        }
      }

      .form /deep/ .mint-cell {
        height: .7rem;
        width: 5.2rem;
        background: rgba(255, 255, 255, 1);
        border-radius: 0.08rem;
        border: 0.02rem solid rgba(221, 221, 221, 1);
        margin-bottom: .2rem;

        input {
          background: rgba(255, 255, 255, 1) !important;
          height: 0.7rem;
          line-height: .7rem;
          width: 100%;
        }
      }

      .otherlogin {
        margin-top: 10px;
      }

      .tips {
        width: 100%;
        flex: 1;
        height: 0.34rem;
        font-size: 0.24rem;
        font-weight: 400;
        color: rgba(255, 255, 255, 1);
        line-height: 0.34rem;
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
      }
    }

    .bottom {
      width: 100%;
      height: 2.04rem;
      display: flex;
      justify-content: center;
      align-items: center;

      .other {
        width: 100%;
        display: flex;
        justify-content: space-around;

        > div {
          width: 0.7rem;
          height: 0.56rem;

          > i {
            font-size: .56rem;
            line-height: .56rem;
          }
        }
      }
    }
  }

</style>



