<template>
  <div class="search">
    <mt-header fixed title="搜索">
      <div class="h_left" slot="left">
        <i @click="back" class="iconfont icon-fanhui"></i>
      </div>
    </mt-header>
    <div class="container">
      <div class="searchbar" >
        <div class="search">
          <div class="icon">
            <i class="iconfont icon-sousuo">
            </i>
          </div>
          <input  @keyup.enter="search" v-model="searchValue" type="text" placeholder="搜索你感兴趣的话题">
        </div>
      </div>


      <!--<mt-search-->
        <!--v-model="searchValue"-->
        <!--cancel-text="取消"-->
        <!--placeholder="搜索你感兴趣的话题"-->
        <!--@keyup.enter.native="search"-->
        <!--style="width:100%;height:auto;"-->
      <!--&gt;-->
      <!--</mt-search>-->

      <div class="recommend">
        <ul>
          <li v-for="(item,index) in searchList" @click="toEssay(item.aid)" :key="index">{{item.title}}</li>
          <!--<li v-for="(item,index) in searchList" :key="index">-->
            <!--<router-link :to="{path:'/essay',query:{aid:item.aid}}">{{item.title}}</router-link>-->
          <!--</li>-->

        </ul>
      </div>
      <div class="empty" v-show="emptyResult">
        很抱歉！无搜索结果
      </div>
      <div class="history" v-if="searchHistory.length&&showHistory">
        <div class="his-title">
          搜索历史
        </div>
        <div class="his">
          <ul>
            <li class="item" v-for="(item,index) in searchHistory" :key="index">
              <div class="word" @click="searchTarget(item.value,$event)">{{item.value}}</div>
              <div class="del" @click="deleteHistory(index)">
                <i class="iconfont icon-shanchu"></i>
              </div>
            </li>
          </ul>
          <div class="clear_history" @click="clearAllHistory">
            <i class="iconfont icon-shanchu1"></i>
            清空搜索历史
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {getStore, setStore} from '@/config/mUtils'
  import {searchRestaurant,} from '@/service/getData'

  export default {
    name: "search",
    data() {
      return {
        searchValue: '',
        searchList: [],
        searchHistory: [],
        showHistory: true,
        emptyResult: false,

      }
    },
    watch: {
      searchValue: function (newvs, oldvs) {
        if (newvs) {
          this.getList();
        }
        if (newvs == '') {
          this.searchList = [];
          this.searchList = this.searchList.slice();
        }
      }
    },
    components: {},
    mounted() {
      //获取搜索历史记录
      if (getStore('searchHistory')) {
        this.searchHistory = JSON.parse(getStore('searchHistory'));
      }
    },
    methods: {
      //返回
      back() {
        this.$router.go(-1);
      },
      //搜索
      search() {
        console.log("搜索");
        this.showHistory = true;
        if (this.searchValue) {
          let arr = {};
          arr.value = this.searchValue;
          this.searchHistory.map((val, index, array) => {
            if (this.searchValue == val.value) {
              this.searchHistory.splice(index, 1);
            }
          });
          this.searchHistory.unshift(arr);
          if (this.searchHistory.length > 5) {
            this.searchHistory.length = 5;
          }
          // this.history.slice()
          this.searchHistory = Object.assign([], this.searchHistory);
          setStore('searchHistory', this.searchHistory);
        } else {
          this.$toast('搜索内容不能为空')
        }
      },
      //点击删除按钮，删除当前历史记录
      deleteHistory(index) {
        this.searchHistory.splice(index, 1);
        setStore('searchHistory', this.searchHistory);
      },
      //清除所有历史记录
      clearAllHistory() {
        this.searchHistory = [];
        this.emptyResult = false; //隐藏搜索为空提示
        setStore('searchHistory', this.searchHistory);
      },
      //跳往问题详情页面
      toEssay(aid) {
        this.$router.push({path:'essay',query:{aid}})
      },
      //点击提交按钮，搜索结果并显示，同时将搜索内容存入历史记录
      async searchTarget(historyValue, $event) {
        console.log(1111111);
        if (historyValue) {
          console.log(22222222222);
          this.searchValue = historyValue;
        } else if (!this.searchValue) {
          console.log(3333333333);
          // this.searchValue = historyValue;
          return
        }
        // //隐藏历史记录
        this.showHistory = false;
        // //获取搜索结果
        this.searchList = await searchRestaurant(this.searchValue);
        this.emptyResult = !this.searchList.length;
        this.search();
        setStore('searchHistory', this.searchHistory)
      },
      //获取问题推荐列表
      async getList() {
        let res = await searchRestaurant(this.searchValue);
        this.searchList = [];
        this.searchList = this.searchList.slice();
        if (res.length > 0) {
          this.searchList = res;
          this.emptyResult = false;
        } else {
          this.emptyResult = true;
        }
      }
    }
  }
</script>

<style scoped lang="scss">
  .searchbar {
    margin-top: .2rem;
    width: 100%;
    padding: 0 .36rem;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;

    .search {
      background: rgba(238, 238, 238, 1);
      width: 6.78rem;
      height: .72rem;
      display: flex;
      border-radius: 4px;

      .icon {
        width: .72rem;
        height: 100%;
        text-align: center;
        line-height: .72rem;
      }

      input {
        width: 100%;
        height: 100%;
        flex: 1;
        background: rgba(238, 238, 238, 1);
      }
    }
  }
  .container {
    padding-top: 40px;
    flex: 1;

    .recommend {
      /*max-height: 3rem;*/
      /*overflow-y: scroll;*/

      ul {
        height: 100%;
        padding: 0.2rem .38rem;

        li {
          height: .6rem;
          line-height: .6rem;
          font-size: 0.28rem;
          font-weight: 400;
          color: rgba(51, 51, 51, 1);
        }
      }
    }

    .empty {
      text-align: center;
      padding: 0 .38rem;
      height: 0.8rem;
      font-size: 0.28rem;
      font-weight: 600;
      color: rgba(51, 51, 51, 1);
      line-height: 0.8rem;
      border-bottom: 1px solid rgba(238, 238, 238, 1);
    }

    .history {
      width: 100%;

      .his-title {
        padding: 0 .38rem;
        height: 0.8rem;
        font-size: 0.28rem;
        font-weight: 600;
        color: rgba(51, 51, 51, 1);
        line-height: 0.8rem;
        border-bottom: 1px solid rgba(238, 238, 238, 1);
      }

      .his {
        ul {
          padding: 0 .38rem;

          .item {
            width: 100%;
            /*height: 0.72rem;*/
            font-size: 0.28rem;
            font-weight: 400;
            color: rgba(51, 51, 51, 1);
            line-height: 0.72rem;
            border-bottom: 1px solid rgba(238, 238, 238, 1);
            display: flex;
            justify-content: space-between;


            .word {
              flex: 1;
            }

            .del {
              text-align: center;
              flex-basis: 0.72rem;;
            }
          }
        }

        .clear_history {
          padding: 0 .38rem;
          height: .72rem;
          line-height: .72rem;

          i {
            margin-right: .1px;
          }
        }
      }

    }
  }
</style>



