import Vue from 'vue'
import Vuex from 'vuex'
import mutations from './mutations'
import actions from './action'
import getters from './getters'

Vue.use(Vuex);

const state = {
	userInfo: null, //用户信息
  // uid: null, //用户id
	login: false,//是否登录
	imgPath:null,//头像地址
  showSidebar:false//侧边栏
};

export default new Vuex.Store({
	state,
	getters,
	actions,
	mutations,
})



