<template>
  <div class="attention">
    <mt-header fixed title="我的关注">
      <div class="h_left" slot="left">
        <i @click="back" class="iconfont icon-fanhui"></i>
      </div>
    </mt-header>
    <div class="container">
      <mt-navbar v-model="selected">
        <mt-tab-item id="1">我的关注</mt-tab-item>
        <mt-tab-item id="2">关注的问题</mt-tab-item>
      </mt-navbar>

      <!-- tab-container -->
      <mt-tab-container v-model="selected">
        <mt-tab-container-item id="1">
          <div class="total">共{{totalpenson}}位</div>
          <div class="person">
            <ul>
              <li v-for="(item,index) in personlist" :key="index">
                <div class="left">
                  <div class="head">
                    <img :src="item.head" alt="">
                  </div>
                  <div class="user"  @click="toUser(item.uid)">
                    <div class="name">{{item.fusername}}</div>
                    <div class="dec ellipsis"> {{item.bio}}</div>
                  </div>
                </div>

                <div class="right">
              <div class="btn">
                <mt-button  @click="toAttr(item.fuid,index)" v-show="item.is_attention==1" type="default">
                  取消关注
                </mt-button>
                <mt-button  @click="toAttr(item.fuid,index)" v-show="item.is_attention!=1" type="danger">
                  关注
                </mt-button>
              </div>
                </div>
              </li>
            </ul>
          </div>

        </mt-tab-container-item>
        <mt-tab-container-item id="2">
          <div class="question">
            <div class="total">共{{totalQues}}条</div>
            <div class="ques_list">
              <ul>
                <li v-for="(item,index) in questionList" :key="index" >
                  <div class="user" @click="toUser(item.uid)">
                    <div class="head">
                      <img :src="item.head" alt="">
                    </div>
                    <div class="del" >
                      <div class="name">{{item.username}} 回答了问题</div>
                      <div class="date">{{item.dateline|dayFmt}}</div>
                    </div>

                  </div>
                  <div class="ques" @click="toEssay(item.aid)">
                    <div class="title ellipsis4">
                      {{item.title}}
                    </div>
                    <div class="dec">
                      {{item.description}}
                    </div>
                  </div>
                  <!--<div class="info">-->
                    <!--<span style="margin-right: .2rem;">123回答</span>-->
                    <!--<span>456关注</span>-->
                  <!--</div>-->
                </li>
              </ul>
            </div>
          </div>


        </mt-tab-container-item>
      </mt-tab-container>
    </div>
  </div>
</template>

<script>
  import {mapState} from 'vuex'
  import {attentionperson, question, toAttention} from '@/service/getData'
  import {imgBaseUrl} from '@/config/env'

  export default {
    name: "attention",
    data() {
      return {
        selected: "1",
        totalpenson: '0',
        totalQues: '0',
        personlist: [],
        questionList: [],
      }
    },
    mounted() {
      setTimeout(() => {
        this.person();
        this.question();
      }, 200)
    },
    computed: {
      ...mapState([
        'showSidebar',
        'userInfo',
        'login'
      ]),
    },
    methods: {
      //返回
      back() {
        this.$router.go(-1);
      },
      //关注的人
      async person() {
        let uid = this.userInfo.uid;
        let res = await attentionperson(uid);
        if (res.length > 0) {
          this.totalpenson = res.length;
          this.personlist = res;
          this.personlist.map((value, index, array) => {
            this.personlist[index].head = imgBaseUrl + "/uc_server/avatar.php?uid=" + value.fuid + "&amp;size=big";
          });
        }
        this.personlist = Object.assign([], this.personlist);
      },
      //跳往用户中心
      toUser(uid) {
        this.$router.push({path:'user',query:{otheruid:uid}})
      },
      //跳往问题详情页面
      toEssay(aid) {
        this.$router.push({path:'essay',query:{aid}})
      },
      //关注的问题
      async question() {
        let uid = this.userInfo.uid;
        let res = await question(uid);
        if (res.result == 200) {
          this.questionList = res.data;
          this.totalQues = res.data.length;
          this.questionList.map((value, index, array) => {
            this.questionList[index].head=imgBaseUrl+'/uc_server/avatar.php?uid='+value.uid+'+&amp;size=small'
          });
          this.questionList=this.questionList.slice()
        } else {
          this.$toast(res.data.msg)
        }
      },
      //关注 取消关注
      async toAttr(uid, index) {
        let res = await toAttention(uid);
        if (res.result == 200) {
          this.$toast(res.msg);
          if (this.personlist.length > 0) {
            this.personlist.map((value, index1, array) => {
              if (index == index1) {
                if (value.is_attention == 1) {
                  this.personlist[index].is_attention = 0;
                } else {
                  this.personlist[index].is_attention = 1;
                }
              }
            })
          }

        } else {
          this.$toast(res.msg);
        }
      },
    }
  }
</script>

<style scoped lang="scss">
  .container {
    padding-top: 40px;
    flex: 1;

    .mint-tab-container-item {
      margin-top: .2rem;

      .total {
        padding: 0 .36rem;
        width: 100%;
        height: 0.6rem;
        font-size: 0.28rem;
        font-weight: 400;
        color: rgba(102, 102, 102, 1);
        line-height: 0.6rem;
        letter-spacing: 1px;
      }

      .person {
        padding: 0 .36rem;
        width: 100%;

        ul {

          width: 100%;
          display: flex;
          flex-direction: column;

          li {
            width: 100%;

            .left {
              display: flex;
              align-items: center;

              .head {
                width: 0.64rem;
                height: 0.64rem;
                overflow: hidden;
                border-radius: 50%;
                margin-right: .2rem;

                img {
                  width: 100%;
                  height: 100%;
                }
              }

              .user {
                .name {
                  width: 4rem;
                  height: 0.5rem;
                  font-size: 0.28rem;
                  font-weight: 600;
                  color: rgba(51, 51, 51, 1);
                  line-height: 0.5rem;
                }

                .dec {
                  width: 4rem;
                  height: 0.5rem;
                  font-size: 0.24rem;
                  font-weight: 400;
                  color: rgba(102, 102, 102, 1);
                  line-height: 0.5rem;
                }
              }

            }

            .right {
              height: 1rem;
              display: flex;
              justify-content: center;
              align-items: center;

              .btn {

                height: 0.56rem;
                border-radius: 0.08rem;
                /deep/ button {
                  width: 100%;
                  height: 100%;
                  font-size: 0.26rem;
                  font-weight: 400;
                  line-height: 0.36rem;
                  letter-spacing: 1px;
                }
              }
            }
          }
        }
      }

      .question {
        .ques_list {
          width: 100%;

          ul {
            width: 100%;
            display: flex;
            flex-direction: column;

            li {
              width: 100%;
              padding: .27rem .36rem;
              border-bottom: 1px solid rgba(238, 238, 238, 1);

              .user {
                width: 100%;
                display: flex;

                .head {
                  height: 100%;
                  display: flex;
                  justify-content: center;
                  margin-right: .3rem;

                  img {
                    width: 0.64rem;
                    height: 0.64rem;
                    overflow: hidden;
                    border-radius: 50%;
                  }
                }

                .del {
                  display: flex;
                  flex-direction: column;
                  justify-content: space-between;

                  .name {
                    font-size: 0.28rem;
                    font-weight: 600;
                    color: rgba(51, 51, 51, 1);
                    line-height: 0.4rem;
                  }

                  .date {
                    font-size: 0.24rem;
                    font-weight: 400;
                    color: rgba(102, 102, 102, 1);
                    line-height: 0.34rem;
                  }
                }
              }

              .ques {

                .title {
                  font-size: 0.34rem;
                  font-weight: 600;
                  color: rgba(51, 51, 51, 1);
                  line-height: 0.48rem;
                  letter-spacing: 1px;
                }

                .dec {
                  margin: .3rem 0;
                  font-size: 0.28rem;
                  font-weight: 400;
                  color: rgba(51, 51, 51, 1);
                  line-height: 0.4rem;
                  letter-spacing: 1px;
                }


              }
              .info {
                height: 0.48rem;
                font-size: 0.24rem;
                font-weight: 400;
                color: rgba(153, 153, 153, 1);
                line-height: 0.48rem;
              }

            }
          }
        }
      }
    }
  }

</style>



