<template>
  <div v-if="showSidebar" class="sidebar" @click="sidebar(false)">
    <div class="sider" @click.stop="sidebar(true)">
      <div v-if="login" class="login"  @click="toUser" >
        <div class="head">
          <img :src="userInfo.head" alt="">
        </div>
        <div class="name">{{userInfo.username}}</div>
      </div>
      <div v-else class="no_login">
        <div class="title">登录小盈问答</div>
        <div class="to_login">
          <router-link to="login">立即登录</router-link>
        </div>
      </div>
      <div class="my_list">
        <ul>
          <li @click="toMessage">我的消息</li>
          <!--<li> <router-link to="attention">我的关注</router-link></li>-->
          <li @click="toAtten"> 我的关注</li>
          <li @click="toUser">个人中心</li>
        </ul>
      </div>
      <div class="exit" @click="toExit">
        退出
      </div>
    </div>
  </div>
</template>

<script>
  import {mapMutations, mapState} from 'vuex'
  import {signout} from '@/service/getData'

  export default {
    name: "sidebar",
    data() {
      return {}
    },
    computed: {
      ...mapState([
        'showSidebar',
        'userInfo',
        'login'
      ]),
    },
    mounted() {
      this.$store.commit('SHOW_SILDER_BAR', {flag: false});
    },
    methods: {
      ...mapMutations([
        'OUT_LOGIN',
      ]),
      //判断是否登录
      isLogin() {
        if (this.login) {
          return true;
        } else {
          this.$toast('请登录后操作');
          return false;
        }
      },
      //跳往个人信息
      toMessage() {
        if (this.isLogin()) {
          this.$router.push('message')
        }
      },
      //跳往关注页面
      toAtten() {
        if (this.isLogin()) {
          this.$router.push('attention')
        }
      },

      //跳往用户中心
      toUser() {
        if (this.isLogin()) {
          this.$router.push({path: 'user', query: {otheruid: this.userInfo.uid}})
        }
      },

      // 点击左侧打开侧边栏
      sidebar(flag) {
        this.$store.commit('SHOW_SILDER_BAR', {flag})
      },
      //退出登录
      async toExit() {
        if (this.isLogin) {
          let res = await signout();
          if (res == 1) {
            this.$toast('退出成功');
            this.OUT_LOGIN()
          } else {
            this.$toast('退出失败');
          }
        }
      }
    }
  }
</script>

<style scoped lang="scss">
  .sidebar {
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .5);
    position: fixed;
    z-index: 100;

    .sider {
      padding-bottom: 1rem;
      width: 4.94rem;
      height: 100vh;
      background: linear-gradient(180deg, rgba(49, 148, 255, 1) 0%, rgba(0, 86, 215, 1) 100%);
      box-shadow: 7px 2px 17px 0px rgba(0, 0, 0, 0.24);
      padding-left: .32rem;
      box-sizing: border-box;

      .no_login {
        width: 100%;
        height: 3.2rem;
        padding-top: 1.28rem;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;

        .title {
          width: 100%;
          height: 20px;
          font-size: 14px;
          text-align: center;
          font-weight: 400;
          color: rgba(255, 255, 255, 1);
          line-height: 20px;
        }

        .to_login {
          width: 72px;
          height: 22px;
          border-radius: 11px;
          border: 1px solid rgba(255, 255, 255, 1);
          text-align: center;
          font-size: 14px;
          font-weight: 400;
          color: rgba(255, 255, 255, 1);
          line-height: 20px;

          a {
            color: rgba(255, 255, 255, 1);
          }
        }

      }

      .login {
        width: 100%;
        height: 3.2rem;
        padding-top: 1.28rem;
        display: flex;
        align-items: center;

        .head {
          margin-right: .56rem;
          width: 1.28rem;
          height: 1.28rem;
          border-radius: 50%;
          overflow: hidden;

          img {
            width: 100%;
            height: 100%;
          }
        }

        .name {
          font-size: 0.28rem;
          font-weight: 400;
          color: rgba(255, 255, 255, 1);
          line-height: 0.4rem;
        }
      }

      .my_list {
        width: 100%;
        margin-top: 1rem;
        margin-bottom: 4rem;

        ul {
          width: 100%;
          display: flex;
          flex-direction: column;

          li {
            font-size: 0.32rem;
            font-weight: 400;
            color: rgba(255, 255, 255, 1);
            line-height: 0.44rem;
            margin-bottom: .68rem;
          }

          a {
            color: rgba(255, 255, 255, 1);
          }
        }
      }

      .exit {
        font-size: 0.28rem;
        font-weight: 400;
        color: rgba(255, 255, 255, 1);
        line-height: 0.4rem;
      }
    }
  }
</style>



