<template>
  <div class="hot">
    <div class="searchbar" @click="toSearch">
      <sear></sear>
    </div>
    <div class="container">
      <div class="title">热门</div>
      <div class="list">
        <ul>
          <li v-for="(item,index) in news" :key="index">
            <div class="tit ellipsis2" @click="toEssay(item.aid)">{{item.title}}</div>

            <div class="opt">
              {{item.list.commentnum}}回答 {{item.list.favtimes}}关注
            </div>
            <div class="art">
              <div class="user">
                <div class="user_left">
                  <div class="head" @click="toUser(item.uid)">
                    <img :src="item.head" alt="">
                  </div>
                  <div class="name" @click="toUser(item.uid)">{{item.username}}</div>
                  <div class="label" @click="toUser(item.uid)">
                    {{item.label}}
                  </div>
                </div>
                <div class="user_right">
                  {{item.list.likenum}}赞
                </div>
              </div>
              <div class="dec" @click="toEssay(item.aid)">
                <div class="dec_left ellipsis4" v-html="item.list.content">
                </div>
                <div class="dec_right" v-if="item.pic1">
                  <img :src="item.pic1" alt="">
                </div>
              </div>
              <div class="more">查看{{item.commentnum}}条评论</div>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
  import {imgBaseUrl} from '@/config/env'
  import {hotnews} from '@/service/getData'
  import sear from './sear'

  export default {
    name: "hot",
    data() {
      return {
        news: []//热门资讯
      }
    },
    components: {
      sear
    },
    mounted() {
      this.gethot()
    },
    methods: {

      //跳往搜索页面
      toSearch() {
        this.$router.push('search')
      },
      //跳往问题详情页面
      toEssay(aid) {
        this.$router.push({path:'essay',query:{aid}})
      },
      //跳往用户中心
      toUser(uid) {
          this.$router.push({path:'user',query:{otheruid:uid}})
      },
      //获取热门消息
      async gethot() {
        this.news = await hotnews();
        if (this.news) {
          this.news.map((value, index, array) => {
            value.head = imgBaseUrl + "/uc_server/avatar.php?uid=" + value.uid + "&amp;size=big"
          });
          this.news = this.news.slice();
        }

      }
    }
  }
</script>

<style scoped lang="scss">
  .hot {
    .title {
      box-sizing: border-box;
      width: 100%;
      padding: 0.2rem .36rem;
      font-size: 0.34rem;
      font-weight: 600;
      color: rgba(51, 51, 51, 1);
      line-height: 0.48rem;
      border-bottom: 1px solid rgba(238, 238, 238, 1);
    }

    .list {
      ul {
        width: 100%;
        display: flex;
        flex-direction: column;

        li {
          width: 100%;
          padding: 0.2rem .36rem;
          border-bottom: 1px solid rgba(238, 238, 238, 1);
          box-sizing: border-box;

          .tit {
            width: 100%;
            font-size: 0.34rem;
            font-weight: 600;
            color: rgba(51, 51, 51, 1);
            line-height: 0.5rem;
            letter-spacing: 1px;
          }

          .opt {
            height: 0.48rem;
            font-size: 0.24rem;
            font-weight: 400;
            color: rgba(153, 153, 153, 1);
            line-height: 0.48rem;
          }

          .art {
            .user {
              display: flex;
              justify-content: space-between;
              line-height: 0.5rem;

              .user_left {
                display: flex;

                .head {
                  width: 0.48rem;
                  height: 0.48rem;
                  border-radius: 50%;
                  overflow: hidden;
                  margin-right: .2rem;

                  img {
                    width: 100%;
                    height: 100%;
                    border-radius: 50%;
                    overflow: hidden;
                  }
                }

                .name {
                  margin-right: .2rem;
                  font-size: 0.24rem;
                  font-weight: 400;
                  color: rgba(51, 51, 51, 1);
                }

                .label {
                  font-size: 0.24rem;
                  font-weight: 400;
                  color: rgba(102, 102, 102, 1);
                }
              }

              .user_right {
                font-size: 0.24rem;
                font-weight: 400;
                color: rgba(102, 102, 102, 1);
              }
            }

            .dec {
              margin-top: 10px;
              display: flex;
              justify-content: space-between;

              .dec_left {
                height: 1.6rem;
                font-size: 0.28rem;
                font-weight: 400;
                color: rgba(51, 51, 51, 1);
                line-height: 0.4rem;
                letter-spacing: 1px;
              }

              .dec_right {
                width: 1.6rem;
                height: 1.6rem;
              }
            }

            .more {
              font-size: 0.24rem;
              font-weight: 400;
              color: rgba(153, 153, 153, 1);
              line-height: 0.5rem;
            }
          }

        }
      }
    }
  }
</style>



