    justify-content: space-between;

    .logo {
      margin: 1rem auto 1rem;
      width: 2.26rem;
      height: .46rem;
      color: #fff;
      text-align: center;
      font-size: 20px;

    }

    .form {
      width: 5.92rem;
      height: 7.56rem;
      background: rgba(255, 255, 255, 1);
      box-shadow: 0px 11px 11px 0px rgba(0, 0, 0, 0.1);
      border-radius: 10px;
      display: flex;
      flex-direction: column;
      align-items: center;
      .title{
        height:0.56rem;
        font-size:.4rem;
        font-weight:400;
        color:rgba(51,51,51,1);
        line-height:0.56rem;
        margin: .46rem auto .62rem;
      }
      .mobile{
        text-align: center;
        height:0.4rem;
        font-size:0.28rem;
        font-weight:400;
        color:rgba(51,51,51,1);
        line-height:0.4rem;
        margin-bottom: .2rem;
      }
      .btn{
        margin: 0 auto;
        .tologin{
          margin-top: .8rem;
          width:5.2rem;
          height:0.84rem;
          background:linear-gradient(90deg,rgba(49,148,255,1) 0%,rgba(0,86,215,1) 100%);
