    .form /deep/ .mint-cell {
      width: 5.2rem;
      background: rgba(255, 255, 255, 1);
      border-radius: 0.08rem;
      border: 0.02rem solid rgba(221, 221, 221, 1);
      margin-bottom: .2rem;

      input {
        background: rgba(255, 255, 255, 1) !important;
