<template>
  <div class="user">
    <mt-header fixed title="用户中心">
      <div class="h_left" slot="left">
        <i @click="back" class="iconfont icon-fanhui"></i>
      </div>
    </mt-header>
    <div class="container">
      <div class="user">
        <div class="user_info">
          <div class="head">
            <img :src="user.head" alt="">
          </div>
          <div class="user_box">
            <div class="name">{{user.username}}</div>
            <div class="bio ellipsis">{{user.bio}}</div>
            <div class="atte">
              <span>{{user.attention}}关注</span>
              &nbsp;
              <span>{{user.fans}}粉丝</span>
            </div>
          </div>
        </div>
        <div class="btn" v-if="other">
          <mt-button @click="toAttr(user.uid)" v-if="user.is_attention==1">取消关注</mt-button>
          <mt-button @click="toAttr(user.uid)" v-else type="danger">关注</mt-button>
        </div>
      </div>
      <div class="nav">
        <mt-navbar v-model="selected">
          <mt-tab-item id="1">动态</mt-tab-item>
          <mt-tab-item id="2">提问</mt-tab-item>
          <mt-tab-item id="3">回答</mt-tab-item>
        </mt-navbar>
      </div>
      <div class="tab">
        <!-- tab-container -->
        <mt-tab-container v-model="selected">
          <mt-tab-container-item id="1">

            <div class="answer">
              <div class="total">
                共{{answerNum}}条
              </div>

              <ul>
                <li v-for="(item,index) in answer" :key="index">
                  <div class="q_user" @click="toUser(item.uid)">
                    <div class="head">
                      <img :src="item.head" alt="">
                    </div>
                    <div class="quest">
                      <div class="name">
                        {{item.username}} &nbsp;回答了问题
                      </div>
                      <div class="date">
                        {{item.dateline|dayFmt}}
                      </div>
                    </div>
                  </div>
                  <div class="q_art" @click="toEssay(item.aid)">
                    <div class="q_tit ellipsis2">
                      {{item.subject}}
                    </div>
                    <div class="content ellipsis4" v-html="item.content">

                    </div>
                    <div class="info" @click="toEssay(item.aid)">
                      <span>{{item.commentnum}}回答</span>&nbsp;
                      <span>{{item.favtimes}}关注</span>
                    </div>
                  </div>
                </li>
              </ul>
            </div>
          </mt-tab-container-item>
          <mt-tab-container-item id="2">

            <div class="question">
              <div class="total">
                共{{questionNum}}条
              </div>
              <ul>
                <li v-for="(item,index) in question" :key="index">
                  <div class="q_user" @click="toUser(item.uid)">
                    <div class="head">
                      <img :src="item.head" alt="">
                    </div>
                    <div class="quest">
                      <div class="name">
                        {{item.username}} &nbsp;提出了问题
                      </div>
                      <div class="date">
                        {{item.dateline|dayFmt}}
                      </div>
                    </div>
                  </div>
                  <div class="q_art" @click="toEssay(item.aid)">
                    <div class="q_tit ellipsis2">
                      {{item.title}}
                    </div>
                    <div class="q_opt">
                      <div class="write">
                        <i class="iconfont icon-huida"></i>
                      </div>
                      <div class="info">
                        <span>{{item.commentnum}}回答</span>&nbsp;
                        <span>{{item.favtimes}}关注</span>
                      </div>
                    </div>
                  </div>
                </li>
              </ul>
            </div>


          </mt-tab-container-item>
          <mt-tab-container-item id="3">

            <div class="answer">
              <div class="total">
                共{{answerNum}}条
              </div>

              <ul>
                <li v-for="(item,index) in answer" :key="index">
                  <div class="q_user" @click="toUser(item.uid)">
                    <div class="head">
                      <img :src="item.head" alt="">
                    </div>
                    <div class="quest">
                      <div class="name">
                        {{item.username}} &nbsp;回答了问题
                      </div>
                      <div class="date">
                        {{item.dateline|dayFmt}}
                      </div>
                    </div>
                  </div>
                  <div class="q_art" @click="toEssay(item.aid)">
                    <div class="q_tit ellipsis2">
                      {{item.subject}}
                    </div>
                    <div class="content ellipsis4" v-html="item.content">

                    </div>
                    <div class="info" @click="toEssay(item.aid)">
                      <span>{{item.commentnum}}回答</span>&nbsp;
                      <span>{{item.favtimes}}关注</span>
                    </div>
                  </div>
                </li>
              </ul>
            </div>

          </mt-tab-container-item>
        </mt-tab-container>
      </div>
    </div>
  </div>
</template>

<script>
  import {member, toAttention, userAnswer, userQuestion} from '@/service/getData'
  import {imgBaseUrl} from '@/config/env'
  import {mapState} from 'vuex'

  export default {
    name: "user",
    data() {
      return {
        uid: '',
        selected: '1',
        user: {},
        question: [],
        questionNum: '0',
        answer: [],
        answerNum: '0',
        other:false,
      }
    },
    computed: {
      ...mapState([
        'userInfo',
        'login'
      ]),
    },
    watch: {
      $route(val, old) {
        this.uid = this.$route.query.otheruid;
        this.getUser(this.uid);
      },
      uid(val, old){
        if(this.uid==this.userInfo.uid){
          this.other=false;
            }else {
          this.other=true;
        }
      }
    },
    mounted() {
      this.uid = this.$route.query.otheruid;
      this.getUser(this.uid)
    },
    methods: {   //返回
      back() {
        this.$router.go(-1);
      },
      //跳往问题详情页面
      toEssay(aid) {
        this.$router.push({path: 'essay', query: {aid}})
      },
      //跳往用户中心
      toUser(uid) {
        this.$router.push({path: 'user', query: {otheruid: uid}})
      },
      //获取页面数据
      async getUser(uid) {
        let res = await member(uid);
        this.user = res;
        this.user.head = imgBaseUrl + "/uc_server/avatar.php?uid=" + this.user.uid + "&amp;size=big";
        let ques = await userQuestion(uid);
        this.question = ques.data;
        this.question.map((value, index, array) => {
          this.question[index].head = imgBaseUrl + "/uc_server/avatar.php?uid=" + value.uid + "&amp;size=big";
        });
        this.question = this.question.slice();
        let answ = await userAnswer(uid);
        this.answer = answ.data;
        this.answer.map((value, index, array) => {
          this.answer[index].head = imgBaseUrl + "/uc_server/avatar.php?uid=" + value.uid + "&amp;size=big";
        });
        setTimeout(() => {
          this.questionNum = this.question.length;
          this.answerNum = this.answer.length;

        }, 50)
      },
      //关注 取消关注
      async toAttr(uid,) {
        let res = await toAttention(uid);
        if (res.result == 200) {
          this.$toast(res.msg);
          if (this.user.is_attention == 1) {
            this.user.is_attention = 0;
          } else {
            this.user.is_attention = 1;
          }
          // if (this.answer.length > 0) {
          //   this.answer.map((value, index1, array) => {
          //     if (index == index1) {
          //
          //     }
          //   })
          // this.answer = Object.assign([], this.answer);
        }else {
          this.$toast(res.msg);
        }


      },
    }
  }
</script>

<style scoped lang="scss">
  .user {
    .container {
      padding-top: 40px;
      flex: 1;

      .user {
        padding: .4rem .36rem;
        box-sizing: border-box;
        display: flex;
        justify-content: space-between;

        .user_info {
          display: flex;
          flex: 1;
          width: 5rem;

          .head {
            flex-basis: 1.56rem;
            height: 1.56rem;
            border-radius: 50%;
            overflow: hidden;
            margin-right: .4rem;

            img {
              width: 100%;
              height: 100%;
            }
          }

          .user_box {
            width: 3rem;
            display: flex;
            flex-direction: column;
            justify-content: space-between;

            .name {
              font-size: 0.34rem;
              font-weight: 600;
              color: rgba(51, 51, 51, 1);
              line-height: 0.48rem;
            }

            .bio {
              font-size: 0.28rem;
              font-weight: 400;
              color: rgba(102, 102, 102, 1);
              line-height: 0.4rem;
            }

            .atte {
              > span {
                font-size: 0.28rem;
                font-weight: bold;
                color: rgba(51, 51, 51, 1);
                line-height: 0.34rem;
              }
            }
          }
        }

        .btn {
          /*flex-basis: 1rem;*/
          display: flex;
          justify-content: flex-end;

          .mint-button--default {
            height: 0.56rem;
            font-size: 0.28rem;
            font-weight: 400;
            color: rgba(102, 102, 102, 1);
          }

          .mint-button--danger {
            height: 0.56rem;
            background: rgba(255, 59, 59, 1);
            border-radius: 0.08rem;
            font-size: 0.28rem;
            font-weight: 400;
            color: rgba(255, 255, 255, 1);
            display: flex;
            justify-content: center;
            align-items: center;
          }
        }
      }

      .nav {
        border-top: 1px solid rgba(238, 238, 238, 1);
        border-bottom: 1px solid rgba(238, 238, 238, 1);

        .mint-navbar {
          width: 60%;
          margin: 0 auto;
        }

        /deep/ .is-selected .mint-tab-item-label {
          font-weight: 600;
        }
      }

      .tab {
        padding-top: 2px;

        .total {
          padding: .28rem .36rem;
          font-size: 0.28rem;
          font-weight: 400;
          color: rgba(102, 102, 102, 1);
          line-height: 0.4rem;
          letter-spacing: 1px;
        }

        .question {
          ul {
            display: flex;
            flex-direction: column;

            li {
              padding: .28rem .36rem;
              border-bottom: 1px solid rgba(238, 238, 238, 1);

              .q_user {
                display: flex;

                .head {
                  width: 0.64rem;
                  height: 0.64rem;
                  border-radius: 50%;
                  overflow: hidden;
                  margin-right: .28rem;

                  img {
                    width: 100%;
                    height: 100%;

                  }
                }

                .quest {
                  display: flex;
                  flex-direction: column;
                  justify-content: space-between;

                  .name {
                    font-size: 0.28rem;
                    font-weight: 600;
                    color: rgba(51, 51, 51, 1);
                    line-height: 0.4rem;
                  }

                  .date {
                    font-size: 0.24rem;
                    font-weight: 400;
                    color: rgba(153, 153, 153, 1);
                    line-height: 0.34rem;
                  }
                }
              }

              .q_art {
                .q_tit {
                  font-size: 0.34rem;
                  font-weight: 600;
                  color: rgba(51, 51, 51, 1);
                  line-height: 0.48rem;
                  letter-spacing: 1px;
                  padding: .28rem 0;
                }

                .q_opt {
                  display: flex;
                  justify-content: space-between;


                  .write {
                    width: 1.12rem;
                    height: 0.56rem;
                    background: rgba(0, 117, 255, 1);
                    border-radius: 0.08rem;
                    display: flex;
                    justify-content: center;
                    align-items: center;

                    i {
                      color: #fff;
                      text-align: center;
                      line-height: .56rem;
                    }
                  }

                  .info {
                    font-size: 0.24rem;
                    font-weight: 400;
                    color: rgba(153, 153, 153, 1);
                    line-height: 0.48rem;
                  }
                }

              }
            }
          }
        }

        .answer {
          ul {
            display: flex;
            flex-direction: column;

            li {
              padding: .28rem .36rem;
              border-bottom: 1px solid rgba(238, 238, 238, 1);

              .q_user {
                display: flex;

                .head {
                  width: 0.64rem;
                  height: 0.64rem;
                  border-radius: 50%;
                  overflow: hidden;
                  margin-right: .28rem;

                  img {
                    width: 100%;
                    height: 100%;

                  }
                }

                .quest {
                  display: flex;
                  flex-direction: column;
                  justify-content: space-between;

                  .name {
                    font-size: 0.28rem;
                    font-weight: 600;
                    color: rgba(51, 51, 51, 1);
                    line-height: 0.4rem;
                  }

                  .date {
                    font-size: 0.24rem;
                    font-weight: 400;
                    color: rgba(153, 153, 153, 1);
                    line-height: 0.34rem;
                  }
                }
              }

              .q_art {
                .q_tit {
                  font-size: 0.34rem;
                  font-weight: 600;
                  color: rgba(51, 51, 51, 1);
                  line-height: 0.48rem;
                  letter-spacing: 1px;
                  padding: .28rem 0;
                }

                .content {
                  font-size: 0.28rem;
                  font-weight: 400;
                  color: rgba(51, 51, 51, 1);
                  line-height: 0.4rem;
                  letter-spacing: 1px;
                }

                .info {
                  font-size: 0.24rem;
                  font-weight: 400;
                  color: rgba(153, 153, 153, 1);
                  line-height: 0.48rem;
                }
              }
            }
          }
        }
      }
    }
  }
</style>



