<template>
    <div class="searchbar">
      <div class="search">
        <div class="icon">
          <i class="iconfont icon-sousuo">
          </i>
        </div>
        <input type="text" placeholder="搜索你感兴趣的话题">
      </div>
    </div>
</template>

<script>
    export default {
        name: "searchbar",
        data() {
            return {}
        },
        mounted() {

        },
        methods: {
          //跳往搜索页面
          toSearch() {
            this.$router.push('search')
          },
        }
    }
</script>

<style scoped lang="scss">
  .searchbar {
    margin-top: .2rem;
    width: 100%;
    padding: 0 .36rem;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;

    .search {
      background: rgba(238, 238, 238, 1);
      width: 100%;
      height: .72rem;
      display: flex;
      border-radius: 4px;

      .icon {
        width: .72rem;
        height: 100%;
        text-align: center;
        line-height: .72rem;
      }

      input {
        width: 100%;
        height: 100%;
        flex: 1;
        background: rgba(238, 238, 238, 1);
      }
    }
  }
</style>



