<template>
  <div class="essay">
    <mt-header fixed title="小盈问答">
      <div class="h_left" slot="left">
        <i @click="back" class="iconfont icon-fanhui"></i>
      </div>
    </mt-header>
    <div class="container">
      <div class="detail">
        <div class="title ellipsis2">
          {{article.title}}
        </div>
        <div class="sunmary">
          {{article.sunmary}}
        </div>
        <div class="btns">
          <div class="write" @click="toWrite">
            <i class="iconfont icon-huida"></i>
          </div>
          <div class="person" @click="getInvite">
            <i class="iconfont icon-yaoqing"></i>
          </div>
          <div v-if="article.is_collect==1" class="collect" @click="toCollect">
            <i style="color:gold;" class="iconfont icon-shoucang"></i>
          </div>
          <div v-else class="collect" @click="toCollect">
            <i class="iconfont icon-shoucang"></i>
          </div>
        </div>
      </div>

      <div class="write_box" v-if="ableWrite">
        <div class="toanswer">
          <textarea v-model="content" name="ans" id="ans" cols="30" rows="10"></textarea>
        </div>
        <div class="toansbtn">
          <mt-button type="primary" :disabled="!ableeditor" @click="toAnswer">回答</mt-button>
        </div>
      </div>
      <div class="invite_box" v-if="!ableInvite">
        <ul>
          <li class="invite" v-for="(item,index) in invite" :key="index">

            <div class="i_left">
              <div class="head">
                <img :src="item.head" alt="">
              </div>
              <div class="info">
                <div class="name">
                  {{item.username}}
                </div>
                <div class="bio ellipsis">
                  {{item.bio||'这家伙很懒,什么都没有留下'}}
                </div>
              </div>
            </div>
            <div class="i_right">
              <div class="person">
                <i v-if="item.is_invite==0" @click="invitePer(item.uid,item.username,)"
                   class="iconfont icon-yaoqing"></i>
                <i v-else style="color: gold" class="iconfont icon-yaoqing"></i>
              </div>
            </div>
          </li>
        </ul>
      </div>
      <div class="comment_list">
        <ul>
          <li v-for="(item,index) in answer" :key="index">
            <div class="user">
              <div class="left" @click="toUser(item.uid)">
                <div class="head">
                  <img :src="item.head" alt="">
                </div>
                <div class="info" @click="toUser(item.uid)">
                  <div class="name">{{item.username}}</div>
                  <div class="label ellipsis">{{item.bio||"这个人很懒,什么都没有留下"}}</div>
                </div>
              </div>
              <div class="right">
                <div class="btn">
                  <mt-button @click="toAttr(item.uid,index)" v-if="item.is_attention==1" type="default">取消关注</mt-button>
                  <mt-button @click="toAttr(item.uid,index)" v-else type="danger">关注</mt-button>
                </div>
              </div>
            </div>
            <div class="content" v-html="item.content">

            </div>
            <div class="more">
              <div class="left">
                <div @click="toLike(item.id,item.uid,item.subject,item.username,index)" v-if="item.islike==0"
                     class="like">
                  <i style="margin-right: .04rem;" class="iconfont icon-dianzan"></i>
                  {{item.favtimes}}
                </div>
                <div v-else class="like">
                  <i style="margin-right: .04rem;color: gold" class="iconfont icon-dianzan"></i>
                  {{item.favtimes}}
                </div>
                <div class="com" @click="todetail(index)">
                  <i style="margin-right: .04rem;" class="iconfont icon-pinglun"></i>
                  {{item.commentnum}}
                </div>
              </div>
              <div class="right">
                查看{{item.commentnum}}条评论
              </div>
            </div>
            <div class="ans-box" v-if="item.ableComment">
              <div class="comment">

                <div class="textarea">
                  <textarea placeholder="写下你的想法" v-model="commentword" name="com" id="commentword" cols="40"
                            rows="1"></textarea>
                </div>
                <div class="btn">
                  <mt-button :disabled="!ableComm"
                             @click="toComment(index,item.id,item.uid,item.username,item.catid,item.subject)"
                             type="primary">评价
                  </mt-button>
                </div>

              </div>
              <div class="ans_list">
                <ul>
                  <li v-for="(el,i) in item.list" :key="i">
                    <div class="top">
                      <div class="use" @click="toUser(el.uid)">
                        <div class="head">
                          <img :src="el.head" alt="">
                        </div>
                        <div class="name">
                          {{ el.username}}
                        </div>
                      </div>
                      <div class="date">
                        {{ el.dateline|dayFmt}}
                      </div>
                    </div>
                    <div class="bottom" v-html="el.message">

                    </div>
                  </li>
                </ul>
              </div>
            </div>
          </li>
        </ul>

      </div>

      <recommend></recommend>
    </div>

  </div>
</template>

<script>
  import {
    ansList,
    article,
    collect,
    comLike,
    comment,
    inviteList,
    response,
    toAttention,
    toInvite
  } from '@/service/getData'
  import {imgBaseUrl} from '@/config/env'
  import recommend from './component/recommend'
  import {mapState} from 'vuex'

  export default {
    name: "essay",
    data() {
      return {
        aid: null,
        acindex: null,
        article: {},
        answer: [],
        invite: [],
        ableWrite: false,
        ableInvite: true,
        content: '',
        ableeditor: false,
        commentword: '',
        ableComm: false,
      }
    },
    mounted() {
      this.aid = this.$route.query.aid;
      this.getArticle(this.aid);
      this.getAnslist();
    },

    watch: {
      $route(val, old) {
        this.aid = this.$route.query.aid;
        this.getArticle(this.aid);
        this.getAnslist();
      },
      content(val, old) {
        var len = 0;
        for (var i = 0; i < val.length; i++) {
          var a = val.charAt(i);
          if (a.match(/[^\x00-\xff]/ig) != null) {
            len += 2;
          } else {
            len += 1;
          }
        }
        if (16 <= len) {
          this.ableeditor = true;

        } else {
          this.ableeditor = false;

        }
      },
      commentword(val, old) {
        var len = 0;
        for (var i = 0; i < val.length; i++) {
          var a = val.charAt(i);
          if (a.match(/[^\x00-\xff]/ig) != null) {
            len += 2;
          } else {
            len += 1;
          }
        }
        if (8 <= len) {
          this.ableComm = true;

        } else {
          this.ableComm = false;

        }
      },

    }
    ,
    computed: {
      ...mapState([
        'userInfo',
        'login'
      ]),
    }
    ,
    components: {
      recommend
    }
    ,
    methods: {
      //判断是否登录
      isLogin() {
        if (this.login && this.userInfo) {
          return true;
        } else {
          this.$toast('请登录后操作');
         setTimeout(()=> {
           this.$router.push('login');
         },500);
          return false;
        }
      },
      //返回
      back() {
        this.$router.go(-1);
      }
      ,
      //获取提问信息
      async getArticle(aid) {
        let res = await article(aid);
        this.article = res;
      }
      ,
      //跳往用户中心
      toUser(uid) {
        this.$router.push({path: 'user', query: {otheruid: uid}})
      }
      ,
      //获取回答列表
      async getAnslist() {
        let aid = this.aid || this.article.id;
        let res = await ansList(aid);
        this.answer = res;
        this.answer.map((value, index, array) => {
          this.answer[index].ableComment = false;
          if (!isNaN(this.acindex) && this.acindex == index) {
            this.answer[index].ableComment = true;
          }
          this.answer[index].head = imgBaseUrl + "/uc_server/avatar.php?uid=" + value.uid + "&amp;size=big";
          this.answer[index].list.map((val, i, arr) => {
            this.answer[index].list[i].head = imgBaseUrl + "/uc_server/avatar.php?uid=" + val.uid + "&amp;size=big";
          })
        });
        this.answer = this.answer.slice();
      }
      ,
      //问题收藏
      async toCollect() {
        if (this.isLogin()) {
          let aid = this.aid;
          let uid = this.article.uid;
          let username = this.article.username;
          let title = this.article.title;
          let res = await collect(aid, uid, username, title);
          if (res.result == 200 || res.result == 201) {
            this.$toast(res.msg);
            if (this.article.is_collect == 1) {
              this.article.is_collect = 0;
            } else {
              this.article.is_collect = 1;
            }
          } else {
            this.$toast(res.msg);
          }
        }
      }
      ,
      //关注 取消关注
      async toAttr(uid, index) {
        if (this.isLogin()) {
          let res = await toAttention(uid);
          if (res.result == 200) {
            this.$toast(res.msg);
            if (this.answer.length > 0) {
              this.answer.map((value, index1, array) => {
                if (index == index1) {
                  if (value.is_attention == 1) {
                    this.answer[index].is_attention = 0;
                  } else {
                    this.answer[index].is_attention = 1;
                  }
                }
              })
              this.answer = Object.assign([], this.answer);
            }

          } else {
            this.$toast(res.msg);
          }
        }
      }
      ,
      //问题回答
      toWrite() {
        this.ableWrite = !this.ableWrite;
        this.ableInvite = true;
      }
      ,
      //获取邀请人员数据
      async invitedata() {
        let aid = this.article.aid;
        let res = await inviteList(aid);
        this.invite = res;
        this.invite.map((value, index, array) => {
          this.invite[index].head = imgBaseUrl + "/uc_server/avatar.php?uid=" + value.uid + "&amp;size=big";
        });
      }
      ,
      //邀请人员列表
      async getInvite() {
        if (this.isLogin()) {
          this.ableWrite = false;
          if (this.invite.length <= 0) {
            this.ableInvite = !this.ableInvite;
            this.invitedata();
          } else {
            this.ableInvite = !this.ableInvite;
          }
        }
      }
      ,
      //邀请人员回答问题
      async invitePer(uid, username2) {
        if (this.isLogin()) {
          let aid = this.article.aid;
          let title = this.article.title;
          let username = this.userInfo.username;
          let res = await toInvite(aid, uid, username, username2, title);
          if (res.result == 200) {
            this.invitedata();
            this.$toast(res.msg);
          } else {
            this.$toast(res.msg);
          }
        }
      }
      ,
      // inviteList
      //调用回答
      async toAnswer() {
        if (this.isLogin()) {
          let article = this.article;
          let aid = article.aid;
          let uid = article.uid;
          let username = article.username;
          let subject = article.title;
          let content = this.content;
          let res = await response(aid, uid, username, subject, content);
          if (res.result == 200) {
            this.getAnslist();
            this.content = '';
            this.$toast(res.msg);
          } else {
            this.$toast(res.msg);
          }
        }
      }
      ,
      //回答评论
      async toLike(id, uid, title, username, index) {
        if (this.isLogin()) {
          let res = await comLike(id, title, uid, username);
          if (res.result == 200) {
            this.$toast(res.msg);
            this.getAnslist();
            // this.answer.map((value, index1, array) => {
            //   if (index1 == index) {
            //     if (this.answer[index].is_like == 0) {
            //       this.answer[index].favtimes == 1;
            //       this.answer[index].commentnum = this.answer[index].commentnum + 1;
            //     } else if (this.answer[index].is_like == 1) {
            //       this.answer[index].favtimes == 1;
            //     }
            //   }
            // })
            this.answer = this.answer.slice();
            // this.invitedata();
          } else {
            this.$toast(res.msg);
          }
        }
      },
      //展示回答下的评论
      todetail(index) {
        this.acindex = index;
        if (this.answer[index].ableComment == true) {
          var flag = true;
        } else {
          var flag = false;
        }
        this.answer.map((value, index1, array) => {
          this.answer[index1].ableComment = false;
        });
        this.answer[index].ableComment = !flag;
        this.answer = this.answer.slice();
      },
      //回答评论
      async toComment(index, aid, uid, username, catid, title) {
        if (this.isLogin) {
          let message = this.commentword;
          let res = await comment(aid, catid, title, message, uid, username);
          if (res.result == 200) {
            this.getAnslist();
            this.commentword = '';
            this.$toast(res.msg);
          } else {
            this.$toast(res.msg);
          }
        }
      }
    }
  }
</script>

<style scoped lang="scss">
  .essay {
    .container {
      padding-top: 40px;
      flex: 1;

      .detail {
        padding: 0 .36rem;
        border-bottom: 1px solid rgba(238, 238, 238, 1);

        .title {
          width: 100%;
          font-size: 0.34rem;
          font-weight: 600;
          color: rgba(51, 51, 51, 1);
          line-height: 0.5rem;
          letter-spacing: 1px;
        }

        .sunmary {
          width: 100%;
          font-size: 0.30rem;
          color: rgba(51, 51, 51, 1);
          line-height: 0.4rem;
          letter-spacing: 1px;
        }

        .btns {
          margin: .18rem 0;
          display: flex;
          justify-content: flex-start;

          > div {
            width: 1.12rem;
            height: 0.56rem;
            border-radius: 0.08rem;
            margin-right: .3rem;
            text-align: center;
            line-height: .56rem;

            > i {
              color: #fff;
            }
          }

          .write {
            background-color: #0075ff;

          }

          .person {
            background-color: #00cd20;
          }

          .collect {
            background-color: #ff7800;

          }
        }
      }

      .toanswer {
        padding: 0 .36rem;
        margin: 10px 0;

        textarea {
          width: 100%;
          height: 100%;
          padding: .3rem;
          box-sizing: border-box;
          border-radius: 6px;
          border: 1px solid rgba(221, 221, 221, 1);
          overflow-y: scroll;
        }
      }

      .toansbtn {
        padding: 0 .36rem;
        display: flex;
        justify-content: flex-end;

        button {
          height: .56rem;
        }
      }

      .invite_box {
        width: 100%;
        background: rgba(248, 248, 248, 1);
        max-height: 5rem;
        overflow-y: scroll;

        ul {
          width: 100%;
          display: flex;
          flex-direction: column;

          li {
            padding: .14rem .36rem;
            display: flex;
            justify-content: space-between;
            align-items: center;

            .i_left {
              display: flex;

              .head {
                width: 0.64rem;
                height: 0.64rem;
                border-radius: 50%;
                overflow: hidden;
                margin-right: .28rem;

                img {
                  width: 100%;
                  height: 100%;
                }
              }

              .info {
                flex-direction: column;
                justify-content: space-between;

                .name {
                  font-size: 0.28rem;
                  font-weight: 400;
                  color: rgba(51, 51, 51, 1);
                  line-height: 0.4rem;
                }

                .bio {
                  height: 0.34rem;
                  font-size: 0.24rem;
                  font-weight: 400;
                  color: rgba(102, 102, 102, 1);
                  line-height: 0.34rem;
                }
              }

            }

            .i_right {
              background-color: #00cd20;
              width: 1.12rem;
              height: 0.56rem;
              border-radius: 0.08rem;
              text-align: center;
              line-height: .56rem;

              i {
                color: #fff;
              }
            }

          }
        }
      }

      .comment_list {
        ul {
          width: 100%;
          display: flex;
          flex-direction: column;

          li {
            padding: .2rem .36rem;
            box-sizing: border-box;
            width: 100%;
            display: flex;
            flex-direction: column;

            .user {
              display: flex;
              justify-content: space-between;
              align-items: center;

              .left {
                display: flex;

                .head {
                  width: 0.72rem;
                  height: 0.72rem;
                  border-radius: 50%;
                  overflow: hidden;
                  margin-right: .2rem;

                  img {
                    width: 100%;
                    height: 100%;
                  }
                }

                .info {
                  .name {
                    width: 50%;
                    font-size: 0.28rem;
                    font-weight: 400;
                    color: rgba(51, 51, 51, 1);
                    line-height: 0.5rem;
                  }

                  .label {

                  }
                }

              }

              .right {
                height: 100%;
                display: flex;
                align-items: center;

                .btn {
                  height: 0.56rem;
                  border-radius: 0.08rem;
                }

                /deep/ button {
                  width: 100%;
                  height: 100%;
                  font-size: 0.26rem;
                  font-weight: 400;
                  line-height: 0.36rem;
                  letter-spacing: 1px;
                }

              }
            }

            .content {
              padding: .2rem 0;
              font-size: 0.28rem;
              font-weight: 400;
              color: rgba(51, 51, 51, 1);
              line-height: 0.4rem;
              letter-spacing: 1px;
            }

            .more {
              display: flex;
              justify-content: space-between;

              .left {
                display: flex;

                > div {
                  width: 1.28rem;
                  height: 0.56rem;
                  border-radius: 0.08rem;
                  color: #fff;
                  margin-right: .16rem;
                  text-align: center;
                  line-height: .56rem;
                }

                .like {
                  background-color: #ff3b3b;
                }

                .com {
                  background-color: #0075ff;
                }
              }

              .right {
                font-size: 0.24rem;
                font-weight: 400;
                color: rgba(153, 153, 153, 1);
                line-height: 0.5rem;
              }
            }

            .ans-box {
              width: 100%;
              background: rgba(248, 248, 248, 1);
              margin: .3rem 0;

              .comment {
                padding: 0.28rem .36rem;

                textarea {
                  padding-left: .34rem;
                  height: 0.68rem;
                  background: rgba(255, 255, 255, 1);
                  border-radius: 0.08rem;
                  border: 0.02rem solid rgba(221, 221, 221, 1);
                }

                .btn {
                  margin-top: .28rem;
                  width: 100%;
                  display: flex;
                  justify-content: flex-end;

                  button {
                    height: 0.56rem;
                    border-radius: 0.08rem;
                  }
                }

              }

              .ans_list {
                ul {
                  display: flex;
                  flex-direction: column;

                  li {
                    .top {
                      display: flex;
                      justify-content: space-between;

                      .use {
                        display: flex;

                        .head {
                          width: 0.48rem;
                          height: 0.48rem;
                          border-radius: 50%;
                          overflow: hidden;
                          margin-right: .28rem;

                          img {
                            width: 100%;
                            height: 100%;
                          }
                        }

                        .name {
                          font-size: 0.24rem;
                          font-weight: 400;
                          color: rgba(102, 102, 102, 1);
                          line-height: 0.5rem;
                        }


                      }

                      .date {
                        font-size: 0.24rem;
                        font-weight: 400;
                        color: rgba(102, 102, 102, 1);
                        line-height: 0.5rem;
                      }

                    }

                    .bottom {
                      margin-left: .72rem;
                      font-size: 0.28rem;
                      font-weight: 400;
                      color: rgba(51, 51, 51, 1);
                      line-height: 0.4rem;
                    }
                  }
                }
              }
            }

          }
        }
      }

    }
  }
</style>



