import fetch from '@/config/fetch'
import {getStore} from '@/config/mUtils'

/**
 * 账号密码登录
 */
export const accountLogin = (phone, password) => fetch('/index/data/login', {phone, password}, 'POST');

/**
 * 获取首页热门
 */
export const hotnews = () => fetch('index/data/question_answer', {}, 'post');


/**
 * 退出登录
 */
export const signout = () => fetch('index/data/login_out');

/**
 * 获取短信验证码
 */

export const mobileCode = (mobile, type) => fetch('index/login/sendCaptcha', {mobile, type}, 'POST');

/**
 * 注册
 */

export const register = (mobile, password, code,) => fetch('index/login/register', {mobile, password, code}, 'POST');

/**
 * 搜索推荐
 */
export const searchRestaurant = (like) => fetch('index/data/sos_question2', {like}, 'POST');
// export const searchRestaurant = (like) => fetch('index/data/sos_question', {like}, 'POST');

/**
 * 关注的人
 */
export const attentionperson = (uid) => fetch('index/data/attention', {uid}, 'POST');
/**
 * 关注  取消关注
 */
export const toAttention = (uid) => fetch('index/data/attention_index', {uid}, 'POST');

/**
 * 关注的问题
 */
export const question = (uid) => fetch('index/data/personal_collect', {uid}, 'POST');
/**
 * 图片上传
 */
export const imgUpload = (image) => fetch('index/Update/upload', {image}, 'POST');

/**
 * 问题提问
 */
export const toQuiz = (title, uid, username, pic1, pic2, pic3, summary) => fetch('index/data/question_add', {
  title,
  uid,
  username,
  pic1,
  pic2,
  pic3,
  summary
}, 'POST');


/**
 * 问题详情
 */
export const article = (aid) => fetch('index/data/question_one', {aid}, 'POST');


/**
 * 问题回答列表
 */
export const ansList = (aid) => fetch('index/data/question_answer_list', {aid}, 'POST');
/**
 * 提问收藏
 */
export const collect = (aid, uid, username, title) => fetch('index/data/question_collect', {
  aid,
  uid,
  username,
  title
}, 'POST');
/**
 * 我的消息
 */
export const message = (uid) => fetch('index/data/message', {uid}, 'POST');


/**
 * 我的消息
 */
export const recommend = () => fetch('index/data/question_hot', 'POST');
/**
 * 用户信息
 */
export const member = (uid) => fetch('index/data/member_info', {uid}, 'POST');

/**
 * 用户提问数据
 */
export const userQuestion = (uid) => fetch('index/data/personal_question', {uid}, 'POST');
/**
 * 用户提问数据
 */
export const userAnswer = (uid) => fetch('index/data/personal_answer', {uid}, 'POST');
/**
 * 邀请人员列表
 */
export const inviteList = (aid) => fetch('index/data/invite_question_list', {aid}, 'POST');
/**
 * 问题回答
 */
export const response = (aid, uid, username, subject, content) => fetch('index/data/answer_add', {
  aid,
  uid,
  username,
  subject,
  content
}, 'POST');
/**
 * 邀请人员回答
 */
export const toInvite = (aid, uid, username, username2, title) => fetch('index/data/invite_question', {
  aid, uid, username, username2, title
}, 'POST');
/**
 * 回答点赞
 */
export const comLike = (aid, title, uid, username) => fetch('index/data/like', {
  aid, title, uid, username
}, 'POST');
/**
 * 回答评论
 */
export const comment = (aid, catid, title, message, uid, username) => fetch('index/data/comment', {
  aid, catid, title, message, uid, username
}, 'POST');



































