import Vue from 'vue'
import App from '../App'
import Router from 'vue-router'
import home from '@/page/home/home'
import login from '@/page/login/login'
import mobile from '@/page/login/mobile'
import register from '@/page/login/register'
import search from '@/page/search/search'
import hot from '@/page/home/children/hot'
import attention from '@/page/attention/attention'
import quiz from '@/page/quiz/quiz'
import essay from '@/page/essay/essay'
import message from '@/page/message/message'
import user from '@/page/user/user'

Vue.use(Router);

// const home = r => require.ensure([], () => r(require('../page/home/home')), 'home');
// const login = r => require.ensure([], () => r(require('../page/login/login')), 'login');
// const mobile = r => require.ensure([], () => r(require('../page/login/mobile')), 'mobile');
// const register = r => require.ensure([], () => r(require('../page/login/register')), 'register');
export default new Router({
  routes: [
    {
      path: '/',
      component: App,
      children: [
        // //地址为空时跳转home页面
        {
          path: '/',
          meta: {requireAuth: false, keepAlive: false, title: '小盈问答'},
          redirect: "/home",
        },
        //首页
        {
          path: '/home',
          meta: {requireAuth: false, keepAlive: false, title: '小盈问答'},
          component: home,
          children: [
            {
              path: '',
              meta: {requireAuth: false, keepAlive: false, title: '小盈问答'},
              component: hot,
            },
            //热门
            {
              path: '/hot',
              meta: {requireAuth: false, keepAlive: false, title: '热门'},
              component: hot,
            },

          ]
        },
        //密码
        {
          path: '/login',
          meta: {requireAuth: false, keepAlive: false, title: '登录'},
          component: login,
        },
        //手机登录
        {
          path: '/mobile',
          meta: {requireAuth: false, keepAlive: false, title: '手机登录'},
          component: mobile,
        },
        //注册
        {
          path: '/register',
          meta: {requireAuth: false, keepAlive: false, title: '密码登录'},
          component: register,
        },
        //搜索
        {
          path: '/search',
          meta: {requireAuth: false, keepAlive: false, title: '搜索'},
          component: search,
        },
        //我的关注
        {
          path: '/attention',
          meta: {requireAuth: true, keepAlive: false, title: '我的关注'},
          component: attention,
        },
        //我的提问
        {
          path: '/quiz',
          meta: {requireAuth: true, keepAlive: false, title: '我的提问'},
          component: quiz,
        },
        //问答详情页面
        {
          path: '/essay',
          meta: {requireAuth: false, keepAlive: false, title: '问题详情'},
          component: essay,
        },
        //我的消息
        {
          path: '/message',
          meta: {requireAuth: true, keepAlive: false, title: '我的消息'},
          component: message,
        },
        //个人,用户中心
        {
          path: '/user',
          meta: {requireAuth: true, keepAlive: false, title: '用户中心'},
          component: user,
        },

      ],
    },
    //地址为空时跳转home页面
    {
      path: '',
      meta: {requireAuth: false, keepAlive: false, title: '小盈问答'},
      redirect: home,
    },
  ]
})



