import {setStore, getStore,removeStore} from '../config/mUtils'
export default {
  GET_LOGININFO(state) {
    let use =JSON.parse(getStore("userInfo"));
    if (use) {
      state.userInfo = JSON.parse(getStore("userInfo"));
      state.login = true;
      // return state.userInfo;
      return state;
    } else {
      return state.userInfo;
    }

  },
}



