<template>
  <div class="home">
    <my-sidebar></my-sidebar>
    <mt-header fixed title="小盈问答">
      <div @click="sidebar(true)" class="h_left" slot="left">
        <i  class="iconfont icon-ego-menu"></i>
      </div>
      <mt-button class="h_right" @click="toQuiz" slot="right">
        <i style="font-size: 24px;" class="iconfont icon-wenda"></i>
      </mt-button>
    </mt-header>
    <div class="container">
      <router-view></router-view>

    </div>

  </div>
</template>

<script>
  import {imgBaseUrl} from '@/config/env'
  import {mapState} from 'vuex'
  import {hotnews} from '../../service/getData'

  export default {
    name: "home",
    data() {
      return {
        value: '',
        news: []//热门资讯
      }
    },
    computed: {
      ...mapState([
        'showSidebar'
      ]),
    },
    mounted() {
      this.gethot();
    },
    methods: {
      //判断是否登录
      isLogin() {
        if (this.login) {
          return true;
        } else {
          this.$toast('请登录后操作');
          return false;
        }
      },
      // 点击左侧打开侧边栏
      sidebar(flag) {
        this.$store.commit('SHOW_SILDER_BAR', {flag})
      },
      //去提问
      toQuiz(){
        this.$router.push('/quiz');
      },
      //获取热门消息
      async gethot() {
        this.news = await hotnews();
        if (this.news) {
          this.news.map((value, index, array) => {
            value.head = imgBaseUrl + "/uc_server/avatar.php?uid=" + value.uid + "&amp;size=big"
          });
          this.news = this.news.slice();
        }

      }

    }
  }
</script>

<style scoped lang="scss">
  .home /deep/ .mint-header {
    color: #fff !important;

    .mint-header-title {
      color: #fff !important;
    }

    .icon-ego-menu {
      color: #FFF;
    }

    .h_right {
      color: #FFF;
    }
  }

  .home {
    display: flex;

  }

  .container {
    padding-top: 40px;
    flex: 1;
  }
</style>



