<template>
  <div class="login">
    <div class="top">
      <div class="logo">小盈问答</div>
      <div class="form">
        <div class="title">登录</div>
        <router-link class="mobile" to="mobile">手机快捷登录</router-link>
        <mt-field disableClear :attr="{ maxlength: 11 }" placeholder="请输入手机号" type="tel" v-model="phone"></mt-field>
        <mt-field disableClear placeholder="请输入密码" type="password" v-model="password"></mt-field>
        <div class="btn" @click="tologin">
          <mt-button class="tologin" type="primary">登录</mt-button>
        </div>
        <router-link to="register" class="register">注册</router-link>
      </div>
      <div class="tips">
        第三方登录
      </div>
    </div>
    <div class="bottom">
      <div class="other">
        <div class="qq">
          <i style="color: #25b5ff;" class="iconfont icon-qq"></i>
        </div>
        <div class="weixin">
          <i style="color: #00cd20;" class="iconfont icon-weixin1"></i>
        </div>
        <div class="weibo">
          <i style="color:#ff3b3b;" class="iconfont icon-weibo"></i>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {imgBaseUrl} from '@/config/env'
  import {mapMutations} from 'vuex'
  import {accountLogin} from '@/service/getData'

  export default {
    name: "login",
    data() {
      return {
        phone: '18279198701',
        password: '123456',
        userInfo: null,
      }
    },
    computed: {
      //判断手机号码
      rightPhoneNumber: function () {
        return /^1\d{10}$/gi.test(this.phone)
      }
    },
    mounted() {

    },

    methods: {
      ...mapMutations([
        'RECORD_USERINFO',
      ]),
      //登录
      async tologin() {
        if (this.rightPhoneNumber) {
          if (!this.password) {
            this.$toast('请输入密码');
          } else {
            var res = await accountLogin(this.phone, this.password);
            if (res.result == 200) {
              this.$toast(res.msg);
              this.userInfo = res.user;
              if (!this.userInfo.uid) {
                this.$toast('未知错误,请重试');
              } else {
                this.userInfo.head= imgBaseUrl + "/uc_server/avatar.php?uid=" + this.userInfo.uid + "&amp;size=big";
                this.userInfo = Object.assign({},this.userInfo);
                this.RECORD_USERINFO(this.userInfo);
                this.$router.push('/home');
              }
            } else {
              this.$toast(res.msg);
            }

          }
        } else {
          this.$toast('手机号码不正确');
        }
      }
    }
  }
</script>

<style scoped lang="scss">
  .top {
    width: 100vw;
    height: 11.3rem;
    background: linear-gradient(180deg, rgba(49, 148, 255, 1) 0%, rgba(0, 86, 215, 1) 100%);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;

    .logo {
      margin: 1rem auto 1rem;
      width: 2.26rem;
      height: .46rem;
      color: #fff;
      text-align: center;
      font-size: 20px;

    }

    .form {
      width: 5.92rem;
      height: 7.56rem;
      background: rgba(255, 255, 255, 1);
      box-shadow: 0px 11px 11px 0px rgba(0, 0, 0, 0.1);
      border-radius: 10px;
      display: flex;
      flex-direction: column;
      align-items: center;

      .title {
        height: 0.56rem;
        font-size: .4rem;
        font-weight: 400;
        color: rgba(51, 51, 51, 1);
        line-height: 0.56rem;
        margin: .46rem auto .62rem;
      }

      .mobile {
        text-align: center;
        height: 0.4rem;
        font-size: 0.28rem;
        font-weight: 400;
        color: rgba(51, 51, 51, 1);
        line-height: 0.4rem;
        margin-bottom: .2rem;
      }

      .btn {
        margin: 0 auto;

        .tologin {
          margin-top: .8rem;
          width: 5.2rem;
          height: 0.84rem;
          background: linear-gradient(90deg, rgba(49, 148, 255, 1) 0%, rgba(0, 86, 215, 1) 100%);
          border-radius: 0.42rem;
        }
      }

      .register {
        margin-top: .4rem;
      }
    }

    .form /deep/ .mint-cell {
      width: 5.2rem;
      background: rgba(255, 255, 255, 1);
      border-radius: 0.08rem;
      border: 0.02rem solid rgba(221, 221, 221, 1);
      margin-bottom: .2rem;

      input {
        background: rgba(255, 255, 255, 1) !important;
        height: 0.84rem;
        line-height: .84rem;
        width: 100%;
      }
    }

    .tips {
      width: 100%;
      flex: 1;
      height: 0.34rem;
      font-size: 0.24rem;
      font-weight: 400;
      color: rgba(255, 255, 255, 1);
      line-height: 0.34rem;
      display: flex;
      justify-content: center;
      align-items: center;
      text-align: center;
    }
  }

  .bottom {
    width: 100%;
    height: 2.04rem;
    display: flex;
    justify-content: center;
    align-items: center;

    .other {
      width: 100%;
      display: flex;
      justify-content: space-around;

      > div {
        width: 0.7rem;
        height: 0.56rem;

        > i {
          font-size: .56rem;
          line-height: .56rem;
        }
      }
    }
  }
</style>



