<template>
  <div class="recommend">
    <div class="title">
      提问榜单
    </div>
    <div class="content">
      <ul>
        <li @click="toEssay(item.aid)" v-for="(item,index) in rec" :key="index">
          <div class="tit ellipsis2">
            {{item.title}}
          </div>
          <div class="num">
            <span style="margin-right: .1rem;">{{item.commentnum}}回答</span>
            <span>{{item.favtimes}}关注</span>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
  import {recommend} from '@/service/getData'

  export default {
    name: "recommend",
    data() {
      return {
        rec: []
      }
    },
    mounted() {
      this.getData()
    },
    methods: {
      //获取数据
      async getData() {
        let res = await recommend();
        this.rec = res;
      },
      //跳往问题详情页面
      toEssay(aid) {
        this.$router.push({path:'essay',query:{aid}})
      },
    }
  }
</script>

<style scoped lang="scss">
  .recommend {
    width: 100%;
    box-sizing: border-box;

    .title {
      font-size: 0.34rem;
      font-weight: 600;
      color: rgba(51, 51, 51, 1);
      line-height: 0.48rem;
      border-top: 1px solid rgba(238,238,238,1);
      padding: 0.2rem .36rem 0;
    }

    .content {
      padding: 0 .36rem;
      ul {
        width: 100%;
        display: flex;
        flex-direction: column;
        li {
          padding: .16rem 0;
          .tit {
            width: 100%;
            font-size: 0.34rem;
            font-weight: 600;
            color: rgba(51, 51, 51, 1);
            line-height: 0.5rem;
            letter-spacing: 1px;
          }

          .num {
            width: 100%;
            font-size:0.24rem;
            font-weight:400;
            color:rgba(153,153,153,1);
            line-height:0.48rem;
          }
        }
      }
    }






  }
</style>



